import React from 'react';
import { motion } from 'framer-motion';
import { Button } from '../Button';

/**
 * ActionButton Component
 * Reusable action button with animation delay
 */
export const ActionButton = ({
  children,
  onClick,
  variant = 'primary',
  size = 'lg',
  delay = 0,
  className = '',
  ...props
}) => {
  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay }}
    >
      <Button
        onClick={onClick}
        variant={variant}
        size={size}
        className={className}
        {...props}
      >
        {children}
      </Button>
    </motion.div>
  );
};

