import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { cn } from '../../utils';
import { Card } from '../Card';
import { Button } from '../Button';

/**
 * MenuOverlay Component
 * Overlay menu v·ªõi animation
 */
export const MenuOverlay = ({
  isOpen,
  onClose,
  items = [],
  className = '',
}) => {
  return (
    <AnimatePresence>
      {isOpen && (
        <>
          {/* Backdrop */}
          <motion.div
            className="fixed inset-0 bg-black/50 backdrop-blur-sm z-40"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={onClose}
          />

          {/* Menu Panel */}
          <motion.div
            className={cn(
              'fixed top-0 right-0 h-full w-80 max-w-[90vw] bg-white shadow-2xl z-50',
              'flex flex-col',
              className
            )}
            initial={{ x: '100%' }}
            animate={{ x: 0 }}
            exit={{ x: '100%' }}
            transition={{ type: 'spring', damping: 25, stiffness: 200 }}
          >
            {/* Header */}
            <div className="p-6 border-b border-gray-200 bg-gradient-to-r from-primary-50 to-secondary-50">
              <div className="flex items-center justify-between">
                <h2 className="text-2xl font-bold text-gray-800">Menu</h2>
                <button
                  onClick={onClose}
                  className="p-2 rounded-lg hover:bg-gray-100 transition-colors"
                  aria-label="ƒê√≥ng menu"
                >
                  <span className="text-2xl">‚úï</span>
                </button>
              </div>
            </div>

            {/* Menu Items */}
            <div className="flex-1 overflow-y-auto p-4 space-y-2">
              {items.length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                  Kh√¥ng c√≥ m·ª•c n√†o
                </div>
              ) : (
                items.map((item, index) => (
                  <motion.div
                    key={item.id || index}
                    initial={{ opacity: 0, x: 20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ delay: index * 0.05 }}
                  >
                    <Card
                      onClick={() => {
                        if (item.onClick) {
                          item.onClick();
                          onClose();
                        }
                      }}
                      className={cn(
                        'cursor-pointer transition-all',
                        item.disabled && 'opacity-50 cursor-not-allowed'
                      )}
                      hover={!item.disabled}
                    >
                      <div className="flex items-center space-x-4">
                        {item.icon && (
                          <div className="text-3xl">{item.icon}</div>
                        )}
                        <div className="flex-1">
                          <h3 className="font-semibold text-gray-800">
                            {item.title}
                          </h3>
                          {item.description && (
                            <p className="text-sm text-gray-500 mt-1">
                              {item.description}
                            </p>
                          )}
                        </div>
                        {item.badge && (
                          <span className="px-2 py-1 bg-primary-100 text-primary-700 rounded-full text-xs font-semibold">
                            {item.badge}
                          </span>
                        )}
                      </div>
                    </Card>
                  </motion.div>
                ))
              )}
            </div>

            {/* Footer */}
            <div className="p-4 border-t border-gray-200 bg-gray-50">
              <Button
                onClick={onClose}
                variant="outline"
                className="w-full"
              >
                ƒê√≥ng
              </Button>
            </div>
          </motion.div>
        </>
      )}
    </AnimatePresence>
  );
};

